@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 100vh;
}

.content-wrap {
    -webkit-flex: 1 1;
            flex: 1 1;
}
/* general */

body, button, input  {
    margin: 0;
    padding: 0;
    font-family: 'Quicksand', sans-serif;
}
.imageContainer img {
    max-width: 100%;
    height: auto;
    display: block;
    border: 2px solid black;
    padding:.5em;
}
.flexbox {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
/* home */
.banner {
    margin: .5em;
    padding: .5em;
}
.banner .imageContainer {
    width: 60%;
    height: auto;
    overflow: hidden;
    -webkit-align-self: right;
            align-self: right;

}

.banner .content {
    width: 40%;
    padding-left: .5em;
}



.banner .content p {
    border-bottom: 2px solid black;
}
.readMore {
    background-color: black;
    color: white;
    padding: .5em;
    border: 3px solid black;
    border-radius: 2em;
    transition: all 100ms ease-out;
}

.readMore:hover {
    color:rgb(102, 101, 101) ;
}


/* blog */
.blog {
    margin: .5em;
    padding: .5em;
    border: 3px solid black;
}
.blog .imageContainer {
    width: 45%;
    height: auto;
    overflow: hidden;
}

.blog .content {
    width: 55%;
    padding-left: .5em;
    padding-right: 1em;
}

.blog .content h3 {
font-weight: normal;
text-decoration: underline;}
.blog .content p{
    padding-bottom: .5em;
    border-bottom: 2px solid black;
}

.blog .content a {
    font-weight: bold;
    text-decoration: underline;
}


/* about */
.about img {
    height: 400px;
    border-radius: 5%;

}
.profilePic {
    padding-top: 1em;
    display: block;
    text-align: center;
}

.resume {
    background-color: black;
    color: white;
    padding: .5em;
    border: 3px solid black;
    border-radius: 2em;
    margin: 1em;
}

.resume:hover {
    color:rgb(102, 101, 101) ;
}

.instaContainer {
    border-top: 5px solid black ;
}
/* contact */

.contactContainer {
    margin: 1em;
}
.contactTitle {
    border-bottom: 2px solid black;

}
.contactContainer h1 {
    margin-left: .5em;
    font-weight: normal;
    }

.formContainer {
    background-color:rgb(236, 233, 233);
    border: 3px solid rgb(236, 233, 233);
    border-radius: 1em;
    padding: 1em;
    margin-top: 1em; 
    font-family: 'Quicksand', sans-serif;
    width: 50%;
}


.inputContainer input {
    width:60%;
}
.inputContainer textarea {
    width: 75%;
    height: 10em;
}
.inputContainer p {
    padding-left: -100px;
}

.formContainer .error {
    color: red
}

.formContainer .submit {
padding-top: 1em;
}

.formContainer .submit input {
    width: 5em;
}
.headerTitle {
    text-align: center;
    font-family: 'Amatic SC', cursive;
    font-size: 4em;
}

.navMenu {
    text-align: center;
    width: auto;
    padding-bottom: .5em;
    border-bottom: 5px solid black;
    
}
.navLink {
padding: .5em;
text-decoration: none;
color: inherit;
font-family: 'Poiret One', cursive; 
font-weight: bold;
font-size: 1.5em;
transition: ease-in-out;
}
.navLink:hover {
    
    text-decoration: underline;
    color: rgb(175, 10, 175);
    }

.footerContainer {
    position: relative;
    bottom: 0;
    width: 100%;  
    border-top: 5px solid black;
    background-color: white;
}

/* link footer section */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
a {
    text-decoration: none;
    color: inherit;
}
*:focus,
*:active {
    outline: none !important;
    -webkit-tap-highlight-color: transparent;
}


.wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.wrapper .icon {
    position: relative;
    background-color: #ffffff;
    border-radius: 50%;
    padding: 15px;
    margin: 10px;
    width: 50px;
    height: 50px;
    font-size: 18px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper .tooltip {
    position: absolute;
    top: 0;
    font-size: 14px;
    background-color: #ffffff;
    color: #ffffff;
    padding: 5px 8px;
    border-radius: 5px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper .tooltip::before {
    position: absolute;
    content: "";
    height: 8px;
    width: 8px;
    background-color: #ffffff;
    bottom: -3px;
    left: 50%;
    -webkit-transform: translate(-50%) rotate(45deg);
            transform: translate(-50%) rotate(45deg);
    transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper .icon:hover .tooltip {
    top: -45px;
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
}

.wrapper .icon:hover span,
.wrapper .icon:hover .tooltip {
    text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.1);
}



.wrapper .linkedin:hover,
.wrapper .linkedin:hover .tooltip,
.wrapper .linkedin:hover .tooltip::before {
    background-color: #2867B2;
    color: #ffffff;
}

.wrapper .instagram:hover,
.wrapper .instagram:hover .tooltip,
.wrapper .instagram:hover .tooltip::before {
    background-color: #E1306C;
    color: #ffffff;
}

.wrapper .github:hover,
.wrapper .github:hover .tooltip,
.wrapper .github:hover .tooltip::before {
    background-color: #333333;
    color: #ffffff;
}


.container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
    gap: 20px;
    padding: 15px;
}  
.container img {
    border: 1px solid black;
    width: 100%;
    height: auto;
    display: block;
    transition: all 100ms ease-out;
}
.container img:hover {
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
}

